import * as React from 'react';
import * as Type from './menuType';

import './menu.css';

class Menu extends React.Component<Type.IMenuProps, Type.IMenuState> {
    private itemIndex: number = 0;
    private subMenuIndex: number = 0;

    constructor(props: Type.IMenuProps) {
        super(props);

        this.state = {
            activeFile: '',
            editFile: [],
            openFolder: []
        };
    }

    private menuItem (key: string, item: string | object, rootItem?: boolean) : any {
        let _item: any[] = [];
        let _fileArr: string[];

        if (rootItem) {
            this.itemIndex = 0;
            this.subMenuIndex = 0;
        }

        if (typeof item === 'string') {
            _fileArr = item.split('/');
            _item.push(<li key={this.itemIndex} className={(() => {
                return this.state.activeFile === item ? 'Menu__item Menu__item--active' : 'Menu__item';
            })()}><a href="javascript:;" onClick={(event) => this.onClickItem(item)}><span className="Menu__item__icon glyphicon glyphicon-file"></span><span className="Menu__item__text">{_fileArr[_fileArr.length - 1]}{this.state.editFile.indexOf(item) !== -1 ? '*' : ''}</span></a></li>);

            this.itemIndex++;

            if (rootItem) {
                return (<ul className="Menu__root">{_item}</ul>);
            } else {
                return _item;
            }
        } else {
            for (let k in item) {
                if (item[k]) {
                    _item.push(this.menuItem(k, item[k]));
                }
            }

            if (rootItem) {
                return (<ul className="Menu__root">{_item}</ul>);
            } else {
                this.subMenuIndex ++;
                return (
                    <li key={this.subMenuIndex - 1} className={(() => {
                        return this.state.openFolder.indexOf(key) !== -1 ? 'Menu__item Menu__item--subMenu Menu__item--subMenu--open' : 'Menu__item Menu__item--subMenu';
                    })()}>
                        <a href="javascript:;" onClick={() => this.onClickSubMenu(key)}><span className={(() => {
                            return this.state.openFolder.indexOf(key) !== -1 ? 'Menu__item__icon glyphicon glyphicon-folder-open' : 'Menu__item__icon glyphicon glyphicon-folder-close';
                        })()}></span><span className="Menu__item__text">{key}</span></a>
                        <ul className="Menu__subMenu">{_item}</ul>
                    </li>
                );
            }
        }
    }

    private onClickItem (file: string) {
        this.setActive(file);

        if (typeof this.props.onChange === 'function') {
            this.props.onChange(file);
        }

    }

    private onClickSubMenu (folder: string) {
        let _subMenu: string[] = this.state.openFolder;

        if (_subMenu.indexOf(folder) === -1) {
            _subMenu.push(folder);
        } else {
            _subMenu.splice(_subMenu.indexOf(folder), 1);
        }

        this.setState({openFolder: _subMenu});
    }

    public setActive(file: string) {
        this.setState({activeFile: file});
    }

    public setEdit(file: string | string[], remove?: boolean) {
        let _files: string[] = typeof file === 'string' ? [file] : file;
        let _editFiles: string[] = this.state.editFile;

        if (remove) {
            for (let i = 0; i < _files.length; i++) {
                if (_editFiles.indexOf(_files[i]) !== -1) {
                    _editFiles.splice(_editFiles.indexOf(_files[i]), 1);
                }
            }

            this.setState({editFile: _editFiles});
        } else {
            for (let i = 0; i < _files.length; i++) {
                if (_editFiles.indexOf(_files[i]) !== -1) {
                    _files.splice(i, 1);
                }
            }

            this.setState({editFile: _editFiles.concat(_files)});
        }
    }

    public hasSave(file: string):boolean {
        return this.state.editFile.indexOf(file) === -1;
    }

    public render() {
        return (
            <div className="Menu">
                {this.menuItem('root', this.props.folderStructure, true)}
            </div>
        );
    }
}

export default Menu;



