import * as React from 'react';
import * as Type from './editorType';
import JSONEditor from 'jsoneditor';

import 'jsoneditor/dist/jsoneditor.css';
import './editor.css';

class Editor extends React.Component<Type.IJsonEditorProps, Type.IJsonEditorState> {
    public editor: any = null;
    constructor(props: Type.IJsonEditorProps) {
        super(props);
        this.state = {
            data: {},
            edit: false
        };
    }

    private onChangeData () {
        this.setState({edit: true});
        if (typeof this.props.onEdit === 'function') {
            this.props.onEdit();
        }
    }

    public newJsonData (jsonData?:object) {
        this.editor.set(jsonData ? jsonData : {});
    }

    public getJsonData (jsonPath:string, temp?: boolean){
        this.setState({edit: false});
        if (jsonPath !== '') {
            // console.log(`${this.props.temp === true ? 'temp' : 'data'}/${jsonPath}`);
            // fetch(`${this.props.temp === true ? 'temp' : 'data'}/${jsonPath}`)
            fetch(`/api/getJson`,
            {
                headers: {
                  'Accept': 'application/json',
                  'Content-Type': 'application/json'
                },
                method: "POST",
                body: JSON.stringify({
                    path: jsonPath,
                    temp: temp === true ? temp : this.props.temp
                })
            })
            .then( (response) => {
                return response.json();
            })
            .then ((json) => {
                this.setState({data: json})
                // console.log(JSONEditor);
                if (this.editor === null) {
                    // JSONEditor.defaults.options.theme = 'bootstrap3';
                    if (document.getElementById("editor_holder")) {
                        this.editor = new JSONEditor(document.getElementById("editor_holder"), {
                            onChange:() => this.onChangeData()
                        });
                    }
                }
                this.editor.set(json);
            });
        }
    }

    public saveJsonData (path?: string, temp?: boolean, afterLoad?: string, afterLoadTemp?: boolean) {
        this.setState({edit: false});
        const data = {
            path: path ? path : this.props.path,
            json: this.editor.get(),
            temp: temp
        };

        fetch(`/api/saveJson`,
        {
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            },
            method: "POST",
            body: JSON.stringify(data)
        })
        .then ((response) => {
            if (afterLoad) {
                this.getJsonData(afterLoad, afterLoadTemp);
            }
        }).catch((error) => {
            console.log(error);
            if (afterLoad) {
                this.getJsonData(afterLoad, afterLoadTemp);
            }
        });
    }

    public componentDidMount() {
        this.getJsonData(this.props.path);
    }

    public componentWillUpdate(props: Type.IJsonEditorProps) {
        if (this.props.path !== props.path) {
            if (this.props.path !== '' && this.state.edit) {
                this.saveJsonData(this.props.path, true, props.path, props.temp);
            } else {
                this.getJsonData(props.path, props.temp);
            }
        }
    }

    public render() {
        return (
            <div className="Editor">
                <div ref="editor_holder" id="editor_holder">
                </div>
            </div>
        );
    }
}

export default Editor;



