import * as React from 'react';
import Editor from './editor/index';
import Menu from './menu/index';


import './jsonEditor.css';

import logo from './logo.svg';

class JsonEditor extends React.Component<any, any> {
    private editor:React.RefObject<Editor>;
    private menu:React.RefObject<Menu>;

    constructor(props: any) {
        super(props);
        this.state = {
            menu: {},
            path: '',
            loadTemp: false
        };
        this.editor = React.createRef();
        this.menu = React.createRef();
    }

    private onEditJson () {
        if (this.menu.current !== null) {
            this.menu.current.setEdit(this.state.path);
        }
    }

    private loadTemp (file: string) :boolean {
        if (this.menu.current !== null && file !== '') {
            return !this.menu.current.hasSave(file);
        } else return false;
    }

    public changeJson (jsonPath: string) {
        this.setState((prevState: any) => ({path: jsonPath, loadTemp: this.loadTemp(jsonPath)}));
    }

    public saveJson () {
        if (this.editor.current !== null) this.editor.current.saveJsonData();
        if (this.menu.current !== null) this.menu.current.setEdit(this.state.path, true);
    }

    public getFolderStructure (){
        // fetch(`folderMenu.json`)
        fetch(`/api/getFolderStructure`,
        {
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            },
            method: "GET"
        })
            .then( (response) => {
                return response.json();
            })
            .then ((json) => {
                this.setState({menu: json})
            });
    }

    public componentDidMount() {
        this.getFolderStructure();
    }

    public render() {
        return (
            <div className="JsonEditor">
                <header className="JsonEditor__header">
                    <img src={logo} className="JsonEditor__logo" alt="logo" />
                    <div className="JsonEditor__buttons">
                        <button className="btn btn-default" type="submit" onClick={(event) => this.saveJson()}><span className="glyphicon glyphicon-save"></span>Save</button>
                    </div>
                </header>
                <div className="row">
                    <div className="col col-xs-4 col-md-2">
                        <Menu folderStructure={this.state.menu} onChange={(file: string) => this.changeJson(file)} ref={this.menu} />
                    </div>

                    <div className="col col-xs-8 col-md-10">
                        <Editor path={this.state.path} onEdit={() => this.onEditJson()} temp={this.state.loadTemp} ref={this.editor} />
                    </div>
                </div>
            </div>
        );
    }
}

export default JsonEditor;



